<template>
    <el-card shadow="always">
        <div slot="header" class="clearfix">
            <el-switch style="float: left; padding: 3px 0;" v-model="template.message_merge" inactive-text="聚合发送"></el-switch>
            <span>消息模板</span>
            <el-button style="float: right; padding: 3px 0;" type="text" v-on:click="pushTemplateData()">保存模板</el-button>
        </div>
<!--        <el-row style="color: #C0C4CC;font-size: 12px;line-height: 1.7;padding-bottom: 20px">-->
<!--            <el-col span="24" style="text-align: right">-->
<!--                <el-link type="primary">查看文档</el-link>-->
<!--            </el-col>-->
<!--        </el-row>-->
        <div>
            <el-input
                type="textarea"
                :autosize="{ minRows: 10, maxRows: 200}"
                placeholder="请输入Golang语法的模板内容"
                v-model="template.message_template"
                resize="none">
            </el-input>
        </div>
    </el-card>
</template>

<script>

export default {
    name: "cTemplate",
    data() {
        return {
            template: {
                message_merge: false,
                message_template: '{{ if eq .N6 "firing" }}\n' +
                    '\n' +
                    '## <font color=\'#FF0000\'>【报警中】服务器{{ .N3 }}</font>\n' +
                    '\n' +
                    '{{ else if eq .N6 "resolved" }}\n' +
                    '\n' +
                    '## <font color=\'#20B2AA\'>【已恢复】服务器{{ .N3 }}</font>\n' +
                    '\n' +
                    '{{ else }}\n' +
                    '\n' +
                    '## 标题：信息通知\n' +
                    '\n' +
                    '{{ end }}\n' +
                    '\n' +
                    '========================\n' +
                    '\n' +
                    '**告警规则**：{{ .N1 }}\n' +
                    '\n' +
                    '**告警级别**：{{ .N2 }}\n' +
                    '\n' +
                    '**主机名称**：{{ .N3 }} \n' +
                    '\n' +
                    '**主机地址**：{{ .N4 }}\n' +
                    '\n' +
                    '**告警详情**：{{ .N5 }}\n' +
                    '\n' +
                    '**告警状态**：{{ .N6 }}\n' +
                    '\n' +
                    '**触发时间**：{{ .N7 }}\n' +
                    '\n' +
                    '**发送时间**：{{ .N8 }}\n' +
                    '\n' +
                    '**规则详情**：[Prometheus控制台](https://www.baidu.com)\n' +
                    '\n' +
                    '**报警详情**：[Alertmanager控制台](https://www.baidu.com)\n',
            }
        }
    },
    components: {},
    methods: {
        //推送数据
        pushTemplateData: function () {
            this.axios.post('/v1/web/template', {
                "request_data": this.template
            }).then(response => {
                console.log(response.data);
                this.$message.success("数据库更新成功...")
            }).catch(err => {
                console.log(err);
            });
        },

        //拉取数据
        pullTemplateData: function () {
            this.axios.get('/v1/web/template').then(response => {
                console.log("========" + response.data["message_template"]);
                if (response.data["message_template"] === undefined || response.data["message_template"] === null || response.data["message_template"] === "") {
                    console.log("数据库里没有数据")
                } else {
                    this.template.message_template = response.data["message_template"];
                    this.template.message_merge = response.data["message_merge"];
                    console.log(response.data);
                }
            }).catch(err => {
                console.log(err);
            });
        }
    }, created() {
        //修改步骤条的值
        this.$store.commit("updateStepsActive", 2);
        //拉取数据
        this.pullTemplateData();
    }
}
</script>

